.lp-intro {
	position: relative;
	margin-bottom: 50px;
}

.lp-intro__skewed {
	position: absolute;
	top: 0;
	bottom: 0;
	right: 0;
	left: 0;
	width: 100%;
	height: 100%;
	background-color: var(--lp-secondary-bg);
	z-index: 0;
	transform: skewY(-3deg);
	transform-origin: top left;
}
